.navigation {
  &__list {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    justify-content: flex-start;
    padding-left: 8px;
    color: $color-default-light;
    list-style-type: none;

    @include vp-767 {
      display: block;
      column-count: 2;
      column-width: 143px;
      margin-bottom: 0;
      padding: 0;
    }
  }

  &__item {
    @include vp-767 {
      margin-bottom: 21px;

      &:first-child {
        margin-bottom: 17px;
      }
    }
  }

  &__link {
    display: block;
    padding: 20px;
    font-size: 14px;
    font-family: $ff-secondary;
    font-weight: 400;
    line-height: 16px;
    color: $color-default-light;

    @include vp-1199 {
      padding: 20px 15px;
    }

    @include vp-767 {
      padding: 0;
      font-size: 16px;
      font-family: $ff-secondary;
      font-weight: 400;
      line-height: 19px;
    }

    &--logo {
      display: none;

      @include vp-767 {
        display: inline-block;
        font-family: $ff-primary;
        font-size: 20px;
        font-weight: 600;
        line-height: 24px;
        letter-spacing: 0.4px;
        text-transform: uppercase;
      }
    }
  }
}
